import React, { Component } from 'react'
import './Sermons.scss';
import {Player} from './audioPlayer/Player';
import VideoComponent from './video/Video';

import audioIcon from '../../images/audio.png';
import movieIcon from '../../images/movie.png';

class Sermons extends Component {
  constructor(props){
    super(props);

    this.state = ({
      isAudioPodcast: true
    })
}

togglePodcast(type){
  let {isAudioPodcast} = this.state;
  this.setState({
      isAudioPodcast : type
  })
}

  render() {
    
    let {isAudioPodcast} = this.state;
    let nav_name1="Audio Podcast", nav_name2="Video Podcast"; 
    return (
      <div className={'sermon'}>
        {isAudioPodcast && <Player/>}
        {!isAudioPodcast && <VideoComponent/>}
        <div className={'bottom-nav'}>
          <div className={'sticky-footer'}>

              <div onClick={()=>this.togglePodcast(true)} className={'one-fourth'} id="search"><img class="sticky-search" src={audioIcon} />
              <p>{nav_name1}</p>
              </div>
      
              <div onClick={()=>this.togglePodcast(false)} className={'one-fourth'} id="filters"><img class="sticky-filters" src={movieIcon} />
              <p>{nav_name2}</p>
              
              </div>
          </div>
      </div>
 
      </div>
    )
  }
}


export default Sermons;
