import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { isNullOrUndefined } from 'util';
import './Login.scss';
import LandingPage from '../LandingPage/LandingPage';
import Discipler from '../Discipler/DisciplerPage';
import BottomNavBar from '../Header/BottomNarBar';
import BBMLogo from '../../images/bmm.jpeg';
import * as pageUrls from '../constants/pageUrls';
import * as UserRoles from '../constants/permissions'; 
import Home from '../Home/Home';
class LoginForm extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
          user: null, 
          username:'',
          password:'',
          role:0
        }
      }

    signIn(username, password) {
        this.setState({
          user: {
            username,
            password,
            role:0
          }
        })
    }
      
    signOut() {
        this.setState({user: null})
    }
      

    isValidUser = () =>{
        return (
            <div>
              { 
                (this.state.user!==null) ? 
                  <LandingPage 
                   user={this.state.user} 
                   onSignOut={this.signOut.bind(this)} 
                  />
                :
                  <LoginForm 
                   onSignIn={this.signIn.bind(this)} 
                  />
              }
            </div>
          );
    }

    componentDidUpdate = () => {
      let { user={}, history } = this.props;

      if(user?.isLoggedIn){

          switch(user?.role ){
            case UserRoles.DISCIPLER:
                history.push(pageUrls.DISCIPLER);
                break;
            case UserRoles.BISHOP:
                history.push(pageUrls.BISHOP);
                break;
            case UserRoles.ADMIN:
                history.push(pageUrls.ADMIN);
                break;
            default:
                break;
          }
      }
  }
    
    render() {
        let {username, password, isLoggedIn} = this.props;
      return (
         <div >
            { (this.state.user) ? 
                <Discipler/> 
            :
                <div className={'login-container'}>
                  <img className={'logo'} src={BBMLogo} />
                  <div className={'title'}>Welcome to Tracker</div>
                  <div className={'form'}>
                      <input type="text" value={username} className={'zocial-dribbble'} placeholder="Enter your email" />
                      <input type="text" value={password} placeholder="Password" />
                      <input type="submit" onClick={()=>{
                          this.signIn(username, password);
                          }
                          } value="Login" />
                      <a href="">Lost your password?</a>
                  </div> 
              </div>
            }
        </div>
      );
    }
  }
  
export default LoginForm
