import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import * as pathUrls from './components/constants/pageUrls';
import Routes from './routes';
import Header from './components/Header/Header';

class DefaultComponent extends Component {

    componentDidMount = () => {
    }

    render() {
        let { history } = this.props;
        let isMobile = window.screen.width <= 480;
        let isHomeUrl = history?.location?.pathname===pathUrls.HOME;
        let headerStyle = isHomeUrl ? 'hide' : '';
        let backgroundStyle = isMobile ? 'mobile' : 'desktop';
        return (
            <div id={'main'} className={`main ${backgroundStyle}`}>
                <div className={`route-container ${headerStyle}`}>
                    <Routes/>
                </div>
            </div>
        );
    }
}

export default connect()(withRouter(DefaultComponent));
