import React, { Component } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import "./Calendar.scss";
import { withRouter } from "react-router-dom";
import * as pageUrls from "../constants/pageUrls";
import $ from "jquery";
import Events from "../Events/Events";
export default class Calendar extends Component {
  displayMenu = menuList => {
    return menuList.map((pageList, index) => {
      let { history } = this.props;

      return (
        <div key={index}>
          <li class="menu__body-element">
            <a class="menu__body-link" href={pageList.path}>
              {pageList.page}
            </a>
          </li>
        </div>
      );
    });
  };

  render() {
    let MenuList = [
      { page: "Home", path: "/home" },
      { page: "About", path: "/about" },
      { page: "Events", path: "/events" },
      { page: "Sermons", path: "/sermons" },
      { page: "Ministries", path: "/ministries" },
      { page: "Registration", path: "registration" },
      { page: "Login", path: "/login" }
    ];

    return (
      <section className={"calendar"}>
        <article>
          <div class="menu">
            <input id="menu--toggle" type="checkbox" />
            <label class="menu--toggle__trigger" for="menu--toggle"></label>
            <label class="menu--toggle__burger" for="menu--toggle"></label>

            <ul class="menu__body">{this.displayMenu(MenuList)}</ul>
          </div>
        </article>

        <Events />
        <article>
          <div class="promotion"></div>
        </article>
      </section>
    );
  }
}
