import React, { Component } from "react";
import discipleIcon from '../../images/disciple.png';
import studyIcon from '../../images/filters.png';
import statsIcon from '../../images/statistics.png';
import profileIcon from '../../images/user.png';
import './BottomNavBar.scss';
class BottomNarBar extends Component {
  render() {
    let nav_name1="Disciplers", nav_name2="Study", nav_name3="Stats", nav_name4="Profile";  
    return (
        <div className={'bottom-nav'}>
            <div className={'sticky-footer'}>

                <div className={'one-fourth'} id="search"><img class="sticky-search" src={discipleIcon} />
                <p>{nav_name1}</p>
                </div>
        
                <div className={'one-fourth'} id="filters"><img class="sticky-filters" src={studyIcon} />
                <p>{nav_name2}</p>
                </div>
        
                <div className={'one-fourth'} id="map"><img class="sticky-map" src={statsIcon} />
                <p>{nav_name3}</p>
                </div>
        
                <div className={'one-fourth'} id="profile"><img class="sticky-radius" src={profileIcon}/>
                <p>{nav_name4}</p>
                </div>
            </div>
        </div>
    );
  }
}


export default BottomNarBar