import React, { Component } from 'react'
import './Video.scss';

class Video extends Component {
    constructor(props){
        super(props);
    }

    render() {
        let {pathUrl} = this.props;
        return (
            <div className={'video'}>
              <iframe className={'video'} src="https://www.youtube.com/embed/vTN8QS1Bb-Q" frameborder="0" allow="accelerometer; encrypted-media; gyroscope;" allowfullscreen>
              </iframe>
            </div>
        )
    }
}

export default  Video;