import React, { Component } from "react";
import PropTypes from "prop-types";
import "./MyDisciple.scss";

export default class TableRow extends Component {
    
  
    render() {
    let { firstName, surname, gender, dob, overallStatus, church } = this.props;
    return (
        <tbody>
        <tr>
            <td >{firstName}</td>
            <td >{surname}</td>
            <td >{gender}</td>
            <td >{dob}</td>
            <td >{church}</td>
            <td >{overallStatus}</td>
        </tr>
      </tbody>
        
    );
  }
}

TableRow.propTypes = {
  firstName: PropTypes.string,
  surname: PropTypes.string,
  gender: PropTypes.string,
  church: PropTypes.string,
  overallStatus: PropTypes.string
};
