import React, { Component } from 'react';
import './Ministries.scss';
import discipleIcon from '../../images/disciple.png';
import studyIcon from '../../images/filters.png';
import statsIcon from '../../images/statistics.png';
import profileIcon from '../../images/user.png';

import yq from './images/yq.jpg';
import ck from './images/ck.png';
import aow from './images/aow.jpg';
import fk from './images/fk.png';
import ob from './images/olea.jpg';



export default class Ministries extends Component {
    constructor(props){
        super(props);
    
        this.state = ({
         imgUrl:yq
        })
    }
    
    switchUrl(url){
      let {imgUrl} = this.state;
      this.setState({
        imgUrl : url
      })
    }

    render() {
       
        let nav_name1="CK", nav_name2="AOW", nav_name3="YQ", nav_name4="FK", nav_name5="Olea-Bond"; 
        let {imgUrl} = this.state;
        
        return (
            <div className={'ministies'}>
       
                <img className={'img-ministries'} src={imgUrl} />
                {
                <div className={'bottom-nav'}>
                    <div className={'sticky-footer'}>

                    <div onClick={()=>this.switchUrl(ck)} className={'one-fourth'} id="search"><img class="sticky-search" src={discipleIcon} />
                    <p>{nav_name1}</p>
                    </div>
            
                    <div onClick={()=>this.switchUrl(aow)} className={'one-fourth'} id="filters"><img class="sticky-filters" src={profileIcon} />
                    <p>{nav_name2}</p>
                    </div>
            
                    <div onClick={()=>this.switchUrl(yq)}  className={'one-fourth'} id="map"><img class="sticky-map" src={statsIcon} />
                    <p>{nav_name3}</p>
                    </div>
            
                    <div onClick={()=>this.switchUrl(fk)}  className={'one-fourth'} id="profile"><img class="sticky-radius" src={profileIcon}/>
                    <p>{nav_name4}</p> 
                    </div>

                    <div onClick={()=>this.switchUrl(ob)}  className={'one-fourth'} id="profile"><img class="sticky-radius" src={studyIcon}/>
                    <p>{nav_name5}</p> 
                    </div>

                    </div>
                </div>
                }
            </div>
        )
    }
}
