import React, { Component } from "react";
import { Zoom } from 'react-slideshow-image';
import './LandingPage.scss';
import app1 from './a.jpg';
import app2 from './ap.jpg';
import app3 from './Apostle_1.jpg';
import app4 from './Apostle_2.jpg';
import app5 from './app.JPG';

const zoomOutProperties = {
  duration: 5000,
  transitionDuration: 500,
  infinite: true,
  indicators: true,
  scale: 0.4,
  arrows: true
}

const slideProperties = {
  duration: 5000,
  transitionDuration: 500,
  infinite: true,
  indicators: false,
  arrows: false,
  pauseOnHover: true

}

class LandingPage extends Component {
  constructor() {
    super();
    
  }

  
  render() {
  
    const images = [
      app1,app2,app3,app4,app5
    ]

    return (
      <div className={'landing-page'}>
        <div className="slide-container">
          <Zoom {...slideProperties}>
            {
              images.map((each, index) => 
              <img  className={'img'} key={index}  src={each} alt="this pic" />)
            }
          </Zoom>
        </div>
      </div>
    )
  }
}

 
export default  LandingPage;